
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { callPhone } from "../../../js/utility.js"
export default {
name: 'about',
components: {
Group,
Cell
},
data() {
return {
itemlist: [{
title: '联系电话',
detail: '',
url: require('../../../assets/ab_phone.png'),
type: 1,
islink: true
}, {
title: '微信公众账号',
detail: 'MXCRM',
url: require('../../../assets/ab_wechat.png'),
type: 2,
islink: false
}, {
title: '电子邮箱',
detail: 'service@crm.com',
url: require('../../../assets/ab_email.png'),
type: 3,
islink: true
}, {
title: '官方网站',
detail: 'www.crm.cc',
url: require('../../../assets/ab_web.png'),
type: 4,
islink: true
}, {
title: '官方网站',
detail: 'www.crm.com',
url: require('../../../assets/ab_web.png'),
type: 5,
islink: true
}]
}
},
methods: {
clickItem(item) {
if (item.type === 1) {
callPhone('4006968650');
} else if (item.type === 4 || item.type === 5) {
window.open("http://" + item.detail , '_system');
}
}
}
}




